.rhap_container {
  border: none;
  box-shadow: none;
  background-color: transparent;
  width: 100%;
  margin: auto;
}

.rhap_bar-color,
.rhap_progress-bar > [class^='rhap_progress'],
.rhap_volume-indicator {
  background-color: red;
}

.rhap_container {
  padding: 0;
}

.rhap_volume-bar {
  background-color: rgba(255, 0, 0, 0.4);

}

.rhap_volume-bar-area {
  display: none;
}

